@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;list-style:none;text-decoration:none;box-sizing:border-box}body{text-align-last:center;width:100vw}body #root{height:100vh;overflow:auto}body #root .header{position:absolute;top:0;left:0;right:0;background-color:#6ee6e6;padding:0.6rem 0;height:9rem;display:flex;justify-content:center;align-items:center;flex-direction:column}body #root .header input{margin-bottom:10px}body #root .header button{margin:5px}body #root .header h1{font-size:1.8rem;padding-bottom:0.5rem}body #root .header input{display:flex;justify-content:center;align-items:center}body #root .header .header--input{height:1.6rem;width:16rem;letter-spacing:1.6px}body #root .list{display:flex;align-items:center;flex-direction:column;font-size:1.6rem;height:60%;margin:7.5rem 0 60px 0}body #root .list .list--items{height:100%;padding:1.5rem 0}body #root .list .list--items li{padding:0.1rem;border-bottom:0.2px solid #8d8b8b}body #root .footer{position:absolute;bottom:0;left:0;right:0;background-color:#187e7e;height:50px;display:flex;justify-content:center;align-items:center}body #root .footer a{padding:0 8px}@media only screen and (min-width: 768px){body{background-color:red}body #root{width:100%;background-color:white}body #root .header{width:768px;margin:0 auto}body #root .list{display:flex;align-items:center;flex-direction:column;font-size:1.6rem;height:70%;margin:7.5rem 0 60px 0}body #root .list .list--items{height:100%;padding:1.5rem 0}body #root .list .list--items li{padding:0.1rem;border-bottom:0.2px solid #8d8b8b}body #root .footer{width:768px;margin:0 auto;position:absolute;bottom:0;left:0;right:0;background-color:#187e7e;height:50px;display:flex;justify-content:center;align-items:center}body #root .footer a{padding:0 8px}}

