@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap');

* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}

body {
  text-align-last: center;
  width: 100vw;

  // font-family: 'open-sans';

  #root {
    height: 100vh;
    overflow: auto;

    .header {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      background-color: rgb(110, 230, 230);
      padding: 0.6rem 0;
      height: 9rem;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;

      input{
        margin-bottom: 10px;
      }

      button{
        margin: 5px;
      }

      h1 {
        font-size: 1.8rem;
        padding-bottom: 0.5rem;
      }

      input {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .header--input {
        height: 1.6rem;
        width: 16rem;
        letter-spacing: 1.6px;
      }
    }

    .list {
      display: flex;
      align-items: center;
      flex-direction: column;
      font-size: 1.6rem;
      height: 60%;
      margin: 7.5rem 0 60px 0;

      .list--items {
        height: 100%;
        padding: 1.5rem 0;

        li {
          padding: 0.1rem;
          border-bottom: 0.2px solid rgb(141, 139, 139);
        }
      }
    }

    .footer {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: rgb(24, 126, 126);
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;

      a {
        padding: 0 8px;
      }
    }
  }
}


@media only screen and (min-width: 768px) {
  body {
    background-color: red;

    #root {
      width: 100%;
      background-color: white;

      .header {
        width: 768px;
        margin: 0 auto;
      }

      .list {
        display: flex;
        align-items: center;
        flex-direction: column;
        font-size: 1.6rem;
        height: 70%;
        margin: 7.5rem 0 60px 0;

        .list--items {
          height: 100%;
          padding: 1.5rem 0;

          li {
            padding: 0.1rem;
            border-bottom: 0.2px solid rgb(141, 139, 139);
          }
        }
      }
      .footer {
        width: 768px;
        margin: 0 auto;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgb(24, 126, 126);
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;

        a {
          padding: 0 8px;
        }
      }
    }
  }
}

